import React from 'react'
import { BRANCH } from '../../Registration/data'
function Student({ credentials}) {
    const FullName = credentials["first_name"] + "  " + credentials["middle_name"] + "  " + credentials["last_name"]
    var branch = {};
    const code = credentials["branch"]
    const branchCode = code < 10 ? "0" + credentials["branch"] : code
    for (var prop in BRANCH) {
        if (BRANCH.hasOwnProperty(prop)) {
            branch[BRANCH[prop]] = prop;
        }
    }
    return (
        <div id="student" class="tab-pane fade active show">
            <div class="table-responsive">
                <table class="table">
                    <tbody>
                        <tr>
                            <td><i class="fa fa-user"></i> Full Name</td>
                            <td>{FullName}</td>
                        </tr>
                        <tr>
                            <td><i class="fa fa-envelope-o"></i> Email ID</td>
                            <td id="vemail">{credentials["email"]}</td>
                        </tr>
                        <tr>
                            <td><i class="fa fa-phone"></i> Mobile Number</td>
                            <td id="vcontact">{credentials["contact"]}</td>
                        </tr>
                        <tr>
                            <td><i class="fa fa-user"></i> Gender</td>
                            <td id="vgender">{credentials["gender"]}</td>
                        </tr>
                        <tr>
                            <td><i class="fa fa-list-ol"></i> Caste</td>
                            <td id="vcaste">{credentials["caste"]}</td>
                        </tr>
                        <tr>
                            <td><i class="fas fa-praying-hands"></i> Religion</td>
                            <td id="vreligion">{credentials["religion"]}</td>
                        </tr>
                        <tr>
                            <td><i class="fa fa-calendar"></i> Addmission Year</td>
                            <td id="vaddmission_year">{credentials["addmission_year"]}</td>
                        </tr>
                        <tr>
                            <td><i class="fas fa-graduation-cap"></i> Semester</td>
                            <td id="vsemester">{credentials["semester"]}</td>
                        </tr>
                        <tr>
                            <td><i class="fas fa-code-branch"></i> Branch</td>
                            <td id="vbranch">{branch[branchCode]}</td>
                        </tr>
                        <tr>
                            <td><i class="fas fa-graduation-cap"></i> Course</td>
                            <td id="vcourse">{credentials["course"]}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default Student
